import React from "react";
import { OutboundLink } from "gatsby-plugin-google-analytics";

type Issue = {
  title: string;
  url: string;
  html_url: string;
}
export class Issues extends React.Component<{}, {issues: Issue[]}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      issues: []
    };
    
    if(typeof window !== "undefined")
    {
      fetch(`https://api.github.com/repos/mogular/powerquery-formatter-examples/issues`).then(r => r.json().then(e => {
        this.setState({
          issues: e
        })
      }));
    }
  }
  
  render()
  {
    return (
      <div>
        <h1><OutboundLink href="https://github.com/mogular/powerquery-formatter-examples/issues">Issues</OutboundLink></h1>
        <div>
          {
            this.state.issues.length == 0 ?  <div className="loader"></div> : ""
          }
          <ul>
          {
            this.state.issues.map((issue, i) => {
              return (
                <li key={i}><OutboundLink target="_blank" href={issue.html_url}>{issue.title}</OutboundLink></li>
              )
            })
          }
          </ul>
        </div>
      </div>
    )
  }
}