import { ApplicationInsights } from '@microsoft/applicationinsights-web';
import { constants } from '../constants';

class AppInsightsServiceClass
{
  private appInsights: ApplicationInsights;

  constructor()
  {
    if(typeof window !== "undefined")
    {
      this.appInsights = new ApplicationInsights({
        config: {
          instrumentationKey: constants.appInsightsInstrumentationKey
        }
      });
      this.appInsights.loadAppInsights();
    }
  }

  trackError(message: string, error: Error)
  {
    this.appInsights.trackException({
      error,
      properties: {
        message
      }
    });
  }
  
  trackFormatError(error: Error, code: string, config: object)
  {
    this.appInsights.trackException({
      error,
      // properties: {
      //   code, 
      //   config: config
      // }
    })
  }
  
  trackFormat()
  {
    this.appInsights.trackEvent({
      name: "FormatterCall",
    });
  }

  setUserId(userId: string) {
    this.appInsights.setAuthenticatedUserContext(userId);
  }

  clearUserId() {
    this.appInsights.clearAuthenticatedUserContext();
  }
}


export const AppInsightsService = new AppInsightsServiceClass();